.inputWrap {
    border: 1px solid rgba(90, 90, 90, 0.23);;
    border-radius: 4px;
}

.inputWrap:focus-within {
    border-color: #000000;
}

.input {
    color: #2C2D2E;
    /*color: #e6e6e6;*/
    width: 100%;
    border: none;
    border-radius: 4px;
    height: 38px;
    margin: 0;
    padding: 0 12px;
    font-size: 15px;
    background: transparent;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif!important;
    font-weight: 400;
    line-height: 20px;
}
